import React from 'react';

import SEO from '../../components/SEO';

import avatar from '../../images/me.png';

import {
  AvatarSection,
  AvatarContainer,
  AvatarDisplay,
  AvatarFrame,
  AvatarImage,
  AvatarInfo,
  AvatarName,
  AvatarRole,
  AvatarSocialMedia,
  Container,
  CubeEffect,
  HomeWrapper,
  SocialMediaButton
} from './styles';

export default function Home() {
  return (
    <HomeWrapper>
      <SEO title='Home' />
      {/*Intro Avatar sections*/ }
      <AvatarSection>
        <CubeEffect/>
        <CubeEffect/>
        <CubeEffect/>
        <CubeEffect/>
        <CubeEffect/>
        <CubeEffect/> 
        <Container>
          <AvatarContainer>
            <AvatarDisplay>
              <AvatarFrame />
              <AvatarImage src={avatar} />
            </AvatarDisplay>
            <AvatarInfo>
              <AvatarName>Gabriel Mazurco</AvatarName>
              <AvatarRole>Desenvolvedor Web</AvatarRole>
              <AvatarSocialMedia>
                <SocialMediaButton>
                  F
                </SocialMediaButton>
                <SocialMediaButton>
                  F
                </SocialMediaButton>
                <SocialMediaButton>
                  F
                </SocialMediaButton>
              </AvatarSocialMedia>
            </AvatarInfo>
          </AvatarContainer> 
        </Container>
      </AvatarSection>
      {/* Section */}
      {/* Section */}
      {/* Section */}
      {/* Section */}
    </HomeWrapper>
  );
}
