import styled from 'styled-components';

export const Container = styled.div`
  width: 100%;
  max-width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  @media (min-width: 576px) { max-width: 540px; }
  @media (min-width: 768px) { max-width: 720px; }
  @media (min-width: 992px) { max-width: 960px; }
`;

export const ContainerFluid = styled.div`
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
`;

export const HomeWrapper = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
`;

export const AvatarSection = styled.div`
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: ${props => props.theme.primary};
  width: 100%;
  height: 100vh;
  overflow: hidden;
`;

export const CubeEffect = styled.div`
  position: absolute;
  top: 80vh;
  left: 45vw;
  width: 10px;
  height: 10px;
  border: 1px solid ${props => props.theme.primaryDark};
  transform-origin: top left;
  transform: scale(0) rotate(0deg) translate(-50%, -50%);
  animation: cube 12s ease-in forwards infinite;
  &:nth-child(2n) {
    border-color: lighten(#0040C1, 10%);
  }
  &:nth-child(2) {
    animation-delay: 2s;
    left: 25vw;
    top: 40vh;
  }
  &:nth-child(3) {
    animation-delay: 4s;
    left: 75vw;
    top: 50vh;
  }
  &:nth-child(4) {
    animation-delay: 6s;
    left: 90vw;
    top: 10vh;
  }
  &:nth-child(5) {
    animation-delay: 8s;
    left: 10vw;
    top: 85vh;
  }
  &:nth-child(6) {
    animation-delay: 10s;
    left: 50vw;
    top: 10vh;
  }
  @keyframes cube {
    from {
      transform: scale(0) rotate(0deg) translate(-50%, -50%);   
      opacity: 1;
    }
    to {
      transform: scale(20) rotate(960deg) translate(-50%, -50%); 
      opacity: 0;
    }
  }
`;

export const AvatarContainer = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 15px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
`;

export const AvatarDisplay = styled.div`
  max-width: 300px;
  padding: 10px;
  border-radius: 50%;
  border: 3px solid ${props => props.theme.secondary};
  position: relative;
`;

export const AvatarFrame = styled.div`
  border-radius: 50%;
	border-width: 3px;
	border-style: solid;
  position: absolute;
  top: -15px;
  left: -15px;
  width: 110%;
  height: 110%;
  border-color: ${props => props.theme.secondary} transparent ${props => props.theme.secondary} transparent;
  animation: rotation 8s infinite linear;
  -webkit-animation: rotation 8s infinite linear;
  @media (max-width: 320px) {
    top: -13px;
    left: -13px;
  }
  @keyframes rotation {
    from {
      transform: rotate(0deg);
      -webkit-transform: rotate(0deg);
    }
    to {
      transform: rotate(359deg);
      -webkit-transform: rotate(359deg);
    }
  }
  @-webkit-keyframes rotation {
    from {
      transform: rotate(0deg);
      -webkit-transform: rotate(0deg);
    }
    to {
      transform: rotate(359deg);
      -webkit-transform: rotate(359deg);
    }
  }
`;

export const AvatarImage = styled.img`
  max-width: 100%;
  border-radius: 50%;
  overflow: hidden;
  object-fit: cover;
`;

export const AvatarInfo = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;

export const AvatarName = styled.h2`
  margin-top: 1.5rem;
  font-size: 1.75rem;
  text-transform: uppercase;
  color: ${props => props.theme.secondary};
  white-space: nowrap;
  text-align: center;
`;

export const AvatarRole = styled.h3`
  margin-top: 0.25rem;
  font-size: 1.25rem;
  text-transform: uppercase;
  color: #FFFFFF;
  white-space: nowrap;
  text-align: center;
`;

export const AvatarSocialMedia = styled.div`
  margin-top: 1rem;
  width: 100%;
  max-width: 250px;
  display: flex;
  justify-content: space-around;
  align-items: center;
`;

export const SocialMediaButton = styled.button`
  width: 50px;
  height: 50px;
  border-radius: 25px;
  border: none;
  outline: none;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #FFFFFF;
  font-weight: bold;
  background-color: ${props => props.theme.secondary};
  &:hover {
    background-color: #FFFFFF;
    color: ${props => props.theme.secondary};
  }
`;
